import axios from 'axios';

import React, { Component } from 'react';

import TextField from '@material-ui/core/TextField';

import endpoints from '../../redux/api/endpoints';
import { parseErrorData } from '../../redux/api/utils';

import { ShiftsTable } from '../manager/Shifts';


export class RamshikShiftList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      shiftList: [],
      
      message: null,
      error: null,
    }
  }

  componentDidMount() {
    const token = localStorage.getItem('token');
    const { shiftList } = this.state
    if (shiftList.length === 0) 
      axios({
        method: 'get',
        url: endpoints.RAMSHIK_SHIFT_LIST,
        headers: {'Authorization': `JWT ${token}` }
      })
      .then(response => {
        this.setState({ ...this.state, shiftList: response.data.results });
      })
  }

  render() {
    const { shiftList } = this.state
    return (
      <div className='mt-2'>
        <div className='card card-style mb-2'>
          <div className='content'>
            <h4 className='mb-2'>  ({shiftList.length})</h4>
            {shiftList.length > 0 
              ? <ShiftsTable shiftList={shiftList} />
              : <h5> </h5>
            }
          </div>
        </div>
      </div>
    )
  }
}