import React, { Component } from 'react';
import { connect } from 'react-redux';
import { useHistory  } from "react-router-dom";

// actions
import AuthActions from '../redux/redux-sauce/auth';


function HeaderComp (props) {
  const histoty = useHistory()

  function handleClickLogout () {
    props.logout()
    histoty.push('/')
  }

  function handleClickMenu () {
    histoty.push('/')
  }

  return (
    <div className='d-flex justify-content-between align-items-baseline px-3 py-2'>
      <h4 onClick={handleClickMenu}>{props.user.rama}</h4>
      <h6 >{props.user.nickname}</h6>
      <button className='btn btn-sx bg-red1-light' onClick={handleClickLogout}>
        
      </button>
    </div>
  )
}

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalOpen: false,
    }
  }

  componentDidMount() {
    const token = localStorage.getItem('token');
    if (token) {
      this.props.checkToken(token);
    }
  }

  render() {
    const { isLoggedIn, fetching, user } = this.props.auth

    return (
      user
      ? <div className='header' >
          <HeaderComp logout={this.props.logout} user={user}/>
        </div>
      : 'net login'
    )
  }
}

const mapStateToProps = (state) => ({
  state: state,
  auth: state.auth,
  routing: state.routing,
})

const mapDispatchToProps = (dispatch) => ({
  checkToken: (token) => dispatch(AuthActions.checkTokenRequest(token)),
  logout: () => dispatch(AuthActions.logoutRequest()),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Header);