 import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect, useHistory } from "react-router-dom";
import { CircularProgress } from '@material-ui/core'

import AuthActions from '../redux/redux-sauce/auth';

import { LoginForm } from '../components/CommonForms';


function MenuItem (props) {
  let history = useHistory()

  function handleClick() {
    history.push(props.to);
  }
  return (
    <div 
      className='card-style mx-5 my-3 pt-2'
      onClick={handleClick}
      style={{
        'background': 'white',
        'width': '300px',
        // 'height': '0px',
        'color': 'white',
        'font-weight': 'bold',
        'font-size': '1em',
        'text-align': 'center',
        'word-break': 'break-word',
        'border-radius': '15px',
        'line-height': '12px',
      }}
    >
      <h5>{props.title}</h5>
    </div>
  )
}

function AfterLogin (props) {
  const { user, logout } = props
  return (
    user.is_ramshik &&
      <div className=' '>
        {/* <MenuItem title={' '} to={'/ramshik/shift/list/'}/> */}
        {user.is_senior_ramshik &&
          <MenuItem title={' '} to={'/ramshik/shift/create_shift/'}/>}
        <MenuItem title={'  '} to={'/ramshik/payouts/'}/>
      </div>,
    (user.is_manager || user.is_kladman) &&
      <div className=' '>
        <div className='my-4'>
          <h3 className='text-center'></h3>
          <MenuItem title={'.  '} to={'/manager/stock/'}/>
          <MenuItem title={''} to={'/manager/resaws/create/'}/>
        </div>

        <div className='my-4'>
          <h3 className='text-center'>   </h3>
          <MenuItem title={'  '} to={'/manager/rawstock/create_income/'}/>
          <MenuItem title={''} to={'/manager/quotas/overview/'}/>
        </div>

        <div className='my-4'>
          <h3 className='text-center'>, </h3>
          <MenuItem title={' '} to={'/manager/shift_list/'}/>
          <MenuItem title={' '} to={'/manager/shift/create_shift/'}/>
        </div>
        
        <div className='my-4'>
          <h3 className='text-center'></h3>
          <MenuItem title={' '} to={'/manager/sales/create_sale/'}/>
          <MenuItem title={' '} to={'/manager/sales/calc/'}/>
          <MenuItem title={' '} to={'/manager/sale_list/'}/>
        </div>
        
        <div className='my-4'>
          <h3 className='text-center'>  </h3>
          <MenuItem title={''} to={'/manager/expenses/'}/>
          <MenuItem title={' '} to={'/manager/ramshik_payments/'}/>
          <MenuItem title={'  '} to={'/manager/daily_report/'}/>
        </div>
      </div>
  )
}


class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: '',
    }
    this.login = this.login.bind(this);
  }

  componentDidMount() {
    
  }

  login () {
    this.props.login(this.props.form.values)
  }

  render() {
    const { isLoggedIn, fetching, user } = this.props.state.auth
    return (
      fetching 
        ? <CircularProgress />
        : isLoggedIn 
            ? <AfterLogin user={user} logout={this.props.logout}/>
            : <LoginForm parentSubmit={this.login} />
    )
  }
}

const mapStateToProps = (state) => ({
  routing: state.routing,
  state: state,
  form: state.form.loginForm
})

const mapDispatchToProps = (dispatch) => ({
  login: (payload) => dispatch(AuthActions.loginRequest(payload)),
  logout: (payload) => dispatch(AuthActions.logoutRequest(payload)),
  checkToken: (token) => dispatch(AuthActions.checkTokenRequest(token)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Main);
