import axios from 'axios';

import React, { Component } from 'react';

import endpoints from '../../redux/api/endpoints';
import { getToday } from '../../components/utils';
import { parseErrorData, createUrlParamsFromFilters } from '../../redux/api/utils';


export function SalesTable (props) {
  const { sales, totals, deleteSale } = props

  return (
    <table className='table table-sm table-responsive'>
      <thead>
        <th>/</th>
        <th></th>
        {/* <th></th> */}
        <th></th>
        <th>e</th>
        {/* <th> </th> */}
        <th></th>
        <th></th>
        <th></th>
        {/* <th className='text-nowrap'>. </th> */}
        <th className='text-nowrap'></th>
        {/* <th> </th> */}
      </thead>
      <tbody>
        {sales.map(sale => 
          <tr>
            <td style={{lineHeight: '15px'}}>
              {sale.date}
              <span className='d-block'>{sale.note}</span>
              <span className='font-13'>{sale.sale_type}</span>
            </td>
            <td className='text-nowrap'>
              {sale.lumber_records.map(lumber =>
                <span className='d-block mb-2' style={{lineHeight: '14px'}}>
                  {lumber.quantity} {lumber.lumber}
                  <span className='d-block font-italic'>{lumber.wood_species}</span>
                  <span className='d-block font-italic'>{lumber.selling_total_cash}</span>
                  <span className='d-block font-italic'>{lumber.selling_price}</span>
                  <span className='d-block font-italic'>{lumber.rama_total_cash}</span>
                  <span className='d-block font-italic'>{lumber.rama_price}</span>
                  {/* <span className='mr-1'>{lumber.quantity}</span> */}
                  {/* <span className='mr-1'>{lumber.volume}3</span>
                  <span className=' d-block'>{lumber.selling_price}</span> */}
                </span>
                )}
            </td>
            {/* <td>{sale.volume}</td> */}
            <td>
              {sale.selling_total_cash}
            </td>
            <td style={{lineHeight: '15px'}}>
              <span className='d-block'>{sale.seller_fee}</span>
              {sale.seller_name && <span className=''>{sale.seller_name}</span>}
            </td>
            {/* <td>
              {sale.rama_total_cash}
            </td> */}
            <td>
              {sale.loader_fee}
            </td>
            <td>
              {sale.kladman_fee}
            </td>
            <td>
              {sale.delivery_fee}
            </td>
            <td>
              <button className='btn btn-xs bg-red1-light' value={sale.id} onClick={deleteSale}></button>
            </td>
            {/* <td>
              {sale.add_expenses}
            </td> */}
            {/* <td>
              {sale.net_rama_cash}
            </td> */}
          </tr>
        )}
        <tr className='font-500 font-16'>
          <td></td>
          <td>-</td>
          {/* <td>{totals.total_volume} </td> */}
          <td>{totals.total_selling_cash}</td>
          <td>{totals.total_seller_fee}</td>
          {/* <td>{totals.total_rama_cash}</td> */}
          <td>{totals.total_loader_fee}</td>
          <td>{totals.total_kladman_fee}</td>
          <td>{totals.total_delivery_fee}</td>
          <td>{totals.total_add_expenses}</td>
          {/* <td>{totals.total_net_rama_cash}</td> */}
        </tr>
      </tbody>
    </table>
  )
}


export class SaleList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      saleList: [],
      totals: {},
      
      message: null,
      error: null,
    }

    this.deleteSale = this.deleteSale.bind(this);
  }

  componentDidMount() {
    const token = localStorage.getItem('token');
    const params = createUrlParamsFromFilters({date_after: getToday()});
    // const params = {}

    axios({
      method: 'get',
      url: endpoints.MANAGER_SALE_LIST,
      params: params,
      headers: {'Authorization': `JWT ${token}` }
    })
    .then(response => {
      this.setState({ ...this.state, saleList: response.data.sales, totals: response.data.totals });
    })
  }

  deleteSale (e) {
    const token = localStorage.getItem('token');

    axios({
      method: 'delete',
      url: endpoints.kladman_delete_sale(e.target.value),
      headers: {'Authorization': `JWT ${token}` }
    })
    .then(response => {
      this.setState({ ...this.state, saleList: response.data.sales, totals: response.data.totals });
    })
    console.log(e.target.value)
  }

  render() {
    const { saleList, totals } = this.state
    return (
      <div className='mt-2'>
        <div className='card card-style mb-2'>
          <div className='content'>
            <h4 className='mb-2'> ({saleList.length})</h4>
            {saleList.length > 0 
              ? <SalesTable sales={saleList} totals={totals} deleteSale={this.deleteSale}/>
              : <h5> </h5>
            }
          </div>
        </div>
      </div>
    )
  }
}